import axios from 'axios'

export function getBaseInfo (params) {
  return new Promise((resolve, reject) => {
    axios({
      url: '/queryPHD',
      methods: 'POST',
      params: params
    })
      .then((res) => {
        resolve(res)
      })
      .catch(function (error) {
        reject(error)
      })
  })
}

export function exportBaseInfo () {
  return new Promise((resolve, reject) => {
    axios({
      url: '/queryExport',
      methods: 'get'
    })
      .then((res) => {
        resolve(res)
      })
      .catch(function (error) {
        reject(error)
      })
  })
}

