        <el-form-item label="姓名" :label-width="formLabelWidth">
          <el-input v-model="uploadname"></el-input>
        </el-form-item>
        <el-form-item label="信息门户号" :label-width="formLabelWidth">
          <el-input v-model="uploadid"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
