<script>
import { getBaseInfo, exportBaseInfo, exportDepartmentInfo, uploadInfo } from './../api/index'
export default {
  name: 'HelloWorld',
  data () {
    return {
      personalvisible: false,
      personalflag: true,
      departmentvisible: false,
      departmentflag: true,
      value: true,
      input: '',
      dialogFormVisible: false,
      dialogVisible: false,
      dialogDepartmentVisible: false,
      dialogUploadFormVisible: false,
      uploadvisible: false,
      password: '',
      personalpassword: '',
      token: '',
      exportPath: '',
      formInline: {
        departments: '',
        id: '',
        name: ''
      },
      form: {
        name: '',
        departments: '',
        path: ''
      },
      formLabelWidth: '120px',
      ruleForm: {
        number: ''
      },
      uploadname: '',
      uploadid: '',
      uploadflag: true
    }
  },
  mounted () {
  },
  methods: {
    ComfirmBaseInfo () {
      const params = {
        'idNum': this.personalpassword
      }
      getBaseInfo(params).then(res => {
        console.log(res.data)
        if (res.data.code === 1) {
          this.$refs.personaldown.href = '/file/downloadbyid?' + res.data.path.split('?')[1]
          this.personalflag = true
        } else {
          this.personalflag = false
        }
      })
      this.$refs.website.removeAttribute('href')
    },
    DownloadFile () {
      this.personalpassword = ''
      this.dialogFormVisible = false
      this.personalvisible = false
      if (this.personalflag === false) {
        this.$message.error('查无此人，请核对证件号码！')
      }
    },
    ExportPath () {
      this.dialogVisible = true
      // this.exportPath = '/collection/file/export'
      exportBaseInfo().then(res => {
        this.token = res.data.data.token
      })
    },
    ManagerExport () {
      this.dialogVisible = false
      // this.exportPath = this.token === this.password ? '/collection/file/export' : '#'
      if (this.token === this.password) {
        this.$refs.website.href = '/file/export'
        // this.exportPath = '/file/export'
      } else {
        this.$message.error('请输入正确的口令！')
        // this.exportPath = ''
        this.$refs.website.removeAttribute('href')
      }
      this.password = ''
    },
    onSuccess (response, file, fileList) {
      console.log(response.data)
      if (response.code === 0) {
        this.$message.error(response.data.message)
      } else {
        this.$message.success(response.data.message)
      }
    },
    DepartmentBaseInfo () {
      // this.dialogDepartmentVisible = false
      const params = {
        'unit': this.formInline.departments,
        'name': this.formInline.name,
        'id': this.formInline.id
      }
      // console.log(params)
      exportDepartmentInfo(params).then(res => {
        console.log(res)
        if (res.data.code === 1) {
          this.$refs.departmentwebsite.href = '/file/downloadbycollege?' + res.data.path.split('?')[1]
          console.log(this.$refs.departmentwebsite.href)
          this.departmentflag = true
        } else {
          this.departmentflag = false
        }
      })
      this.$refs.departmentwebsite.removeAttribute('href')
    },
    DepartmentDown () {
      this.formInline.name = ''
      this.formInline.departments = ''
      this.formInline.id = ''
      this.dialogDepartmentVisible = false
      this.departmentvisible = false
      if (this.departmentflag === false) {
        this.$message.error('身份认证失败，请确认身份信息！')
      }
    },
    UploadBaseInfo () {
      const params = {
        'name': this.uploadname,
        'id': this.uploadid
      }
      uploadInfo(params).then(res => {
        console.log(res)
        if (res.data.code === 0) {
          this.uploadflag = false
          this.$refs.upload.disabled = true
          console.log(this.$refs.upload.disabled)
        } else {
          // this.$refs.upload.action = '/collection/file/upload'
          this.$refs.upload.disabled = false
          this.uploadflag = true
          console.log(this.$refs.upload.disabled)
        }
      })
    },
    UploadFile () {
      this.dialogUploadFormVisible = false
      this.uploadvisible = false
      this.uploadname = ''
      this.uploadid = ''
      if (this.uploadflag === false) {
        this.$message.error('身份认证失败，请确认身份信息！')
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a {
  text-decoration:none;
  color: #fff;
}
.index-top {
    width: 100%;
