    border-top: 2px solid #005668;
}
.index-topStyle{
  padding: 60px;
  width: 800px;
  position: absolute;
  left: 50%;
  margin-left: -400px;
  display: flex;
  flex-direction: column;
}
.index-img {
  width: 800px;
}
.index-img-msg {
    padding-bottom:35px;
    font-size:15px;
}
.index-img-msg span{
    margin-right:20px;
    font-size:50px;
    color: #005668;
}
.index-topStyle3 {
    width: 800px;
    text-align: center;
    margin-top: 50px;
}
.info-container {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}
.info-container .input {
  width: 200px;
}
.el-button {
  font-size: 20px;
}
.button {
  padding: 0px 60px;
  display: flex;
  justify-content: space-around;
}
.footer {
    box-sizing: border-box;
    width: 100%;
    text-align: center;
    margin: 30px 0;
    line-height: 16px;
    font-size: 14px;
    padding: 0 20px;
}
</style>



